/* Global settings and elements
---------------------------------------------------------*/

* {
  margin: 0;
  padding: 0;
}

span {
  opacity: 0;
  transition: all 0.5s ease 0s;
}

a {
  text-decoration: none;
  border: none;
  background: none;
  color: black;
  cursor: pointer;
}

/* top of the page
---------------------------------------------------------*/

.topLink {
  font-size: 15px;
  margin: 40px;
  margin-left: 0;
  font-family: monospace;
}

.topLink:hover span {
  opacity: 1;
}

/* Greetings and photo
---------------------------------------------------------*/

.body {
  background: rgba(203, 134, 84, 0.295);
  /* background: linear-gradient(139deg, rgba(203, 133, 84, 1) 0%, rgba(212, 134, 78, 1) 15%, rgba(242, 193, 134, 1) 49%, rgba(255, 249, 235, 1) 100%); */
  font-family: monospace;
  display: flex;
  flex-direction: column;
  height: 1200px;
}

.body go {
  background: rgba(223, 223, 223, 0.295);
}
  
.links {
  display: flex;
  justify-content: right;
}

.grid {
  margin: 0;
  margin-top: 50px;
  display: grid;
  grid-template-columns: 1fr minmax(70ch, 1fr) minmax(200px, 300px) 1fr;
  grid-template-areas:
    ". text image ."
    ". copyable copyable ."
    ". line line ."
    ". . footer .";
  grid-gap: 1rem;
}

.title {
  font-size: 3rem;
}

.name {
  font-size: 20px;
  margin-top: 20px;
}

.bio {
  padding-top: 10px;
  font-size: 15px;
  text-align: justify;
  text-justify: inter-word;
}

.text {
  font-family: Monospace;
  grid-area: text;
}

img {
  max-width: 100%;
}

.image {
  justify-self: end;
  align-self: end;
  max-width: 60%;
  grid-area: image;
  padding-right: 20px;
}

.me {
  border-radius: 10%;
  border: 4px solid #fff;
  box-shadow: 11px 11px 48px 0 rgb(0 0 0 / 12%);
}

@media screen and (max-width: 800px) {
  .me {
    display: none;
  }

  .grid {
    grid-template-columns: 1fr auto auto 1fr;
    grid-template-areas:
      ". text text ."
      ". copyable copyable ."
      ". line line ."
      ". . footer .";
    grid-gap: 0rem;
    margin-left: 20px;
  }
}

/* CV paragraph to copy
---------------------------------------------------------*/
.cvSection {
  margin-top: 30px;
}

.copyable {
  grid-area: copyable;
  width: 100%;
}

.topic {
  text-transform: uppercase;
  font-size: 15px;
  font-weight: bold;
  margin-bottom: 5px;
}

.icon {
  width: 100%;
  height: 100%;
}

.section {
  display: flex;
  flex-direction: reverse;
}

.sectionText {
  width: 95%;
}

.infoLine {
  display: grid;
  grid-template-columns: auto 1fr;
}

.copyText {
  width: auto;
  margin-bottom: 5px;
  transition: all 0.5s ease 0s;
  text-decoration: underline;
  text-decoration-color: rgba(0, 0, 0, 0);
}

.period {
  display: flex;
  justify-content: right;
}

.displayText {
  text-align: justify;
  text-justify: inter-word;
}

.copyButton {
  width: 40px;
  height: 40px;
  margin-right: 10px;
  margin-left: 20px;
  background-color: transparent;
  border-radius: 10%;
  border: none;
  transition: all 0.5s ease 0s;
  outline: none;
}

.copyButton:hover {
  transform: translateY(-7px);
}

.copyButton:active {
  transform: translateY(0px);
}

.section:hover .copyText {
  text-decoration-color: rgba(0, 0, 0);
}

@media screen and (max-width: 800px) {
  .copyButton {
    margin-right: 0px;
  }
}

/* footer
---------------------------------------------------------*/

.footerDivider {
  height: 1px;
  box-sizing: border-box;
  width: 100%;
  background-color: #222;
  align-self: center;
  grid-area: line;
  margin-top: 70px;
}

.footer {
  margin-top: 40px;
  grid-area: footer;
  display: grid;
  justify-content: right;
}

.footerName {
  display: flex;
  justify-content: right;
  font-size: 2.5rem;
}

.footerLink {
  font-size:0.97rem;
  display: flex;
  justify-content: right;
  /* font-size: 15px; */
}

.footerPostCode {
  display: flex;
  justify-content: right;
  font-size: 1.95rem;
}

/* Gradient taken from cssgradient.io 
   base on: https://www.arlenmccluskey.com/about 
*/

/* spots animation
---------------------------------------------------------*/

.animationWrapper {
  overflow: hidden;
  position: absolute;
  left: 0px;
  top: -100px;
  z-index: -1;
  width: 100%;
  height: 1200px;
  isolation: isolate;
}




.spot {
  --size: 700px;
  display: none;
  position: absolute;
  left: calc(var(--size) / 3 * -1.5);
  top: calc(var(--size) / 3 * -1.5);
  width: var(--size);
  height: var(--size);
  mix-blend-mode: soft-light;
  border-radius: var(--size);
  filter: blur(calc(var(--size) / 6));
  animation: dotAnimation ease-in-out 30s infinite;
  will-change: transform; 
}

@supports (mix-blend-mode: soft-light) and (filter: blur(1px)) {
  .animationWrapper {
    /* Remove the fallback background image when the features are supported */
    background: none;
  }
  
	.spot {
    /* Only show the animated dots for browsers that support the required features */
    display: block;
  }
}
  
  .spot1 {
    background: #ed2a24;
  }
  
  .spot2 {
    background: #f48937;
    animation-delay: -10s;
  }
  
  .spot3 {
    background: #a92c5a;
    animation-delay: -20s;
  }


@keyframes dotAnimation {
  0% {
    transform: translate(0, 0);
  }
  33% {
    transform: translate(calc(var(--size) / 1.2), 0);
  }
  66% {
    transform: translate(0, calc(var(--size) / 1.2));
  }
  100% {
    transform: translate(0, 0);
  }
}
